

'use client';

import { useState, useEffect } from 'react';
import { useSearchParams } from 'next/navigation';
import { PageHeader } from '@/components/page-header';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { ScrollArea } from '@/components/ui/scroll-area';
import { conversations as initialConversations, drivers } from '@/lib/data';
import type { Conversation, Message } from '@/lib/types';
import { cn } from '@/lib/utils';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { format, formatISO, parseISO } from 'date-fns';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Send, User, Building, Car, Trash2, List } from 'lucide-react';

const contactTypeIcons = {
    Passenger: <Car className="h-4 w-4" />,
    Agent: <User className="h-4 w-4" />,
    Driver: <User className="h-4 w-4" />,
    Shipper: <Building className="h-4 w-4" />,
};

function SmsPageContent() {
  const searchParams = useSearchParams();
  const [conversations, setConversations] = useState<Conversation[]>(initialConversations);
  const [selectedConversation, setSelectedConversation] = useState<Conversation | null>(null);
  const [newMessage, setNewMessage] = useState('');
  const [filter, setFilter] = useState<'all' | 'responded'>('all');

  useEffect(() => {
    const driverId = searchParams.get('driverId');
    if (driverId) {
      const driver = drivers.find(d => d.id === driverId);
      if (driver) {
        let conversation = conversations.find(c => c.contactName === `Driver ${driver.user.name}`);
        
        if (!conversation) {
          // Create a new conversation if it doesn't exist
          const initialMessage: Message = {
            id: `msg-${Date.now()}`,
            sender: 'me',
            content: 'Hi',
            timestamp: new Date().toISOString(),
          };

          const newConversation: Conversation = {
            id: `conv-${Date.now()}`,
            contactName: `Driver ${driver.user.name}`,
            contactType: 'Driver',
            lastMessage: initialMessage.content,
            lastMessageTime: initialMessage.timestamp,
            messages: [initialMessage],
          };
          
          setConversations(prev => [newConversation, ...prev]);
          setSelectedConversation(newConversation);
        } else {
          setSelectedConversation(conversation);
        }
      }
    } else if (conversations.length > 0) {
      setSelectedConversation(conversations[0]);
    }
  // We only want this to run when the driverId in the URL changes.
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchParams]);

  const handleSendMessage = () => {
    if (newMessage.trim() === '' || !selectedConversation) return;
    
    const message: Message = {
      id: `msg-${Date.now()}`,
      sender: 'me',
      content: newMessage,
      timestamp: new Date().toISOString(),
    };
    
    const updatedConversations = conversations.map(conv => {
        if (conv.id === selectedConversation.id) {
            const newMessages = [...conv.messages, message];
            const updatedConv = {
                ...conv,
                messages: newMessages,
                lastMessage: newMessage,
                lastMessageTime: new Date().toISOString()
            };
            setSelectedConversation(updatedConv);
            return updatedConv;
        }
        return conv;
    });

    setConversations(updatedConversations);
    setNewMessage('');
  };

  const handleClearResponded = () => {
    const unresponded = conversations.filter(conv => {
        const lastMessage = conv.messages[conv.messages.length - 1];
        return lastMessage && lastMessage.sender === 'other';
    });
    setConversations(unresponded);
    setSelectedConversation(unresponded.length > 0 ? unresponded[0] : null);
    setFilter('responded');
  };

  const handleShowAll = () => {
    setConversations(initialConversations);
    if(initialConversations.length > 0) {
        const driverId = searchParams.get('driverId');
        if (driverId) {
            const driver = drivers.find(d => d.id === driverId);
            const conversation = driver ? initialConversations.find(c => c.contactName === `Driver ${driver.user.name}`) : null;
            setSelectedConversation(conversation || initialConversations[0]);
        } else {
            setSelectedConversation(initialConversations[0]);
        }
    } else {
        setSelectedConversation(null);
    }
    setFilter('all');
  };
  
  const displayedConversations = filter === 'all' ? conversations : conversations.filter(conv => {
    const lastMessage = conv.messages[conv.messages.length - 1];
    return lastMessage && lastMessage.sender === 'other';
  });

  return (
    <>
      <PageHeader
        title="SMS & Chat"
        description="Communicate with passengers, drivers, agents, and shippers."
      />
      <div className="flex-1 grid grid-cols-1 md:grid-cols-3 lg:grid-cols-4 gap-4 h-[calc(100vh-200px)]">
        <Card className="md:col-span-1 lg:col-span-1 flex flex-col">
          <CardHeader>
             <div className="flex items-center justify-between">
              <CardTitle>Conversations</CardTitle>
               {filter === 'all' ? (
                <Button variant="ghost" size="sm" onClick={handleClearResponded}>
                  <Trash2 className="h-4 w-4 mr-2" />
                  Clear
                </Button>
              ) : (
                <Button variant="ghost" size="sm" onClick={handleShowAll}>
                  <List className="h-4 w-4 mr-2" />
                  Show All
                </Button>
              )}
            </div>
          </CardHeader>
          <CardContent className="p-0 flex-1">
            <ScrollArea className="h-full">
              <div className="p-2 space-y-1">
                {displayedConversations.sort((a,b) => parseISO(b.lastMessageTime).getTime() - parseISO(a.lastMessageTime).getTime()).map(conv => (
                  <div
                    key={conv.id}
                    onClick={() => setSelectedConversation(conv)}
                    className={cn(
                      'p-3 rounded-lg cursor-pointer transition-colors',
                      selectedConversation?.id === conv.id
                        ? 'bg-accent text-accent-foreground'
                        : 'hover:bg-accent/50'
                    )}
                  >
                    <div className="flex items-center justify-between">
                        <div className="font-semibold">{conv.contactName}</div>
                        <div className="text-xs text-muted-foreground">{format(parseISO(conv.lastMessageTime), 'p')}</div>
                    </div>
                    <div className="flex items-center justify-between mt-1">
                        <p className="text-sm text-muted-foreground truncate pr-4">{conv.lastMessage}</p>
                        <div className="flex items-center gap-1.5 text-xs text-muted-foreground">
                            {contactTypeIcons[conv.contactType]}
                            <span>{conv.contactType}</span>
                        </div>
                    </div>
                  </div>
                ))}
              </div>
            </ScrollArea>
          </CardContent>
        </Card>

        <Card className="md:col-span-2 lg:col-span-3 flex flex-col h-full">
          {selectedConversation ? (
            <>
              <CardHeader className="border-b">
                 <div className="flex items-center gap-3">
                    <Avatar>
                        <AvatarFallback>{selectedConversation.contactName.charAt(0)}</AvatarFallback>
                    </Avatar>
                    <div>
                        <CardTitle>{selectedConversation.contactName}</CardTitle>
                         <p className="text-sm text-muted-foreground">{selectedConversation.contactType}</p>
                    </div>
                </div>
              </CardHeader>
              <CardContent className="flex-1 p-0">
                <ScrollArea className="h-full">
                    <div className="p-6 space-y-4">
                        {selectedConversation.messages.map(message => (
                            <div key={message.id} className={cn(
                                'flex items-end gap-2',
                                message.sender === 'me' ? 'justify-end' : 'justify-start'
                            )}>
                                {message.sender === 'other' && <Avatar className="h-8 w-8"><AvatarFallback>{selectedConversation.contactName.charAt(0)}</AvatarFallback></Avatar>}
                                <div className={cn(
                                    'max-w-xs lg:max-w-md p-3 rounded-2xl',
                                    message.sender === 'me'
                                    ? 'bg-primary text-primary-foreground rounded-br-none'
                                    : 'bg-muted rounded-bl-none'
                                )}>
                                    <p className="text-sm">{message.content}</p>
                                    <p className="text-xs mt-1 opacity-70 text-right">{format(parseISO(message.timestamp), 'p')}</p>
                                </div>
                            </div>
                        ))}
                    </div>
                </ScrollArea>
              </CardContent>
              <div className="p-4 border-t">
                  <div className="flex items-center gap-2">
                      <Input 
                        placeholder="Type a message..."
                        value={newMessage}
                        onChange={(e) => setNewMessage(e.target.value)}
                        onKeyDown={(e) => e.key === 'Enter' && handleSendMessage()}
                      />
                      <Button onClick={handleSendMessage}>
                          <Send className="h-4 w-4"/>
                          <span className="sr-only">Send</span>
                      </Button>
                  </div>
              </div>
            </>
          ) : (
            <div className="flex items-center justify-center h-full text-muted-foreground">
              <p>Select a conversation to view messages</p>
            </div>
          )}
        </Card>
      </div>
    </>
  );
}

export default function SmsPage() {
    return (
        <SmsPageContent />
    )
}
